import React, { Component } from "react";
import M from "materialize-css/dist/js/materialize.min.js";
import "materialize-css/dist/css/materialize.min.css";
import "./sidenav.css";
import Menu from "../../Assets/SVG/menu.svg";
import { NavLink } from "react-router-dom";

class Sidebar extends Component {
  state = {
    white: false
  };

  setWhite = () => {
    if (
      this.props.location.pathname.includes("signin") ||
      this.props.location.pathname.includes("signup")
    ) {
      this.setState({
        white: true
      });
    } else {
      this.setState({
        white: false
      });
    }
  };
  componentDidMount() {
    var elem = document.querySelector(".sidenav");
    var instance = M.Sidenav.init(elem, {
      edge: "left",
      inDuration: 250
    });
    this.setWhite();
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevProps.location.pathname !== this.props.location.pathname) {
      this.setWhite();
    }
  }

  render() {
    return (
      <div className="show-on-small">
        <ul id="slide-out" className="sidenav">
          <NavLink className="black-text add" to="/register">
            How it works
          </NavLink>

          <NavLink className="black-text" to="">
            Developer
          </NavLink>

          <NavLink className="black-text" to="/signin">
            Sign In
          </NavLink>

          <NavLink className="black-text" to="/signup">
            Sign Up
          </NavLink>
        </ul>

        <div className="hide-on-med-and-up">
          <div
            style={{ background: this.state.white ? "white" : "#00aeb5" }}
            class="nav-wrapper sm-nav "
          >
            <div style={{color:this.state.white? '#00aeb5': 'white'}} className="sm-left">OPay</div>
            <div className="sm-right">
              <a href="#" data-target="slide-out" className="sidenav-trigger">
                <img src={Menu} alt="" />
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Sidebar;
