import React, { Component } from "react";
import "./navbar.css";
import { NavLink } from "react-router-dom";

class Navbar extends Component {
  state = {
    white: false
  };

  setWhite = () => {
    if (
      this.props.location.pathname.includes("signin") ||
      this.props.location.pathname.includes("signup")
    ) {
      this.setState({
        white: true
      });
    } else {
      this.setState({
        white: false
      });
    }
  };
  componentDidMount() {
    this.setWhite();
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevProps.location.pathname !== this.props.location.pathname) {
      this.setWhite();
    }
  }

  render() {
    return (
      <div className="nav-holder hide-on-small-only">
        <div
          style={{ background: this.state.white ? "#ffffff" : "#00aeb5" }}
          className="navbar-wrapper"
        >
          <div
            style={{ color: this.state.white ? "#00aeb5" : "white" }}
            className="nav-left"
          >
            OPay
          </div>
          <div className="nav-right">
            <NavLink to="">How it works</NavLink>
            <NavLink to="">Developer</NavLink>
            <NavLink to="/signin">Sign In</NavLink>
            <NavLink
              style={{ background: this.state.white ? "#00aeb5" : "none" }}
              className="sign-up"
              to="/signup"
            >
              Sign Up
            </NavLink>
          </div>
        </div>
      </div>
    );
  }
}

export default Navbar;
